.chatareaheader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: #5f5b8e; */
    border-bottom: 1px solid #41404038;
    color: lightgray;
    padding: 1em;
}

.chatareaheader span{
    text-transform: capitalize;
}

.chatareaheader_options{
    display: flex;
    gap: 0.6em;
    height: 32px;
    align-items: center;
}

.chatareaheader_options ion-icon{
    font-size: 18px;
}

@media(max-width: 28em){
    .chatareaheader_options ion-icon{
        font-size: 15px;
    }
}